.jg-main {
    height: auto;
    width: 100vw;
    max-width: 100%;
    /* opacity: 0.1; */
    /*787b*/
}

.main-heading {
    position: relative;
    width: 100%;
    height: 100vh;
}

.selection {
    position: relative;
    z-index: 2;
    width: 50%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.selection-item:not(:first-child) {
    margin-top: 10px;
}

.selection-item {
    cursor: pointer;
    width: 200px;
    padding: 10px;
    text-align: center;
    border: 1px solid;
    color: #ffffff;
    text-decoration: none;
}

#about-me-video {
    opacity: 0;
    filter: grayscale(0%);
    transition: opacity 0.5s ease, filter 1s ease;
}

#about-me-video.active {
    opacity: 1;
    /* filter: grayscale(80%); */
    /* display: none; */
    /*787b*/
}

.about-me-video-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-me-video-opacitizer {
    /* display: none; */
    /*787b*/
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.65);
    z-index: 1;
}

.jg-main-h1, .jg-main-h2, .selection-item {
    transform: perspective(400px) rotateX(90deg);
}

.jg-main-h1 {
    font-size: 72px;
    margin: 0;
}

.jg-main-h2 {
    font-size: 26px;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}

section.about-me {
    font-size: 60px;
    text-align: center;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 100px;
    max-width: 1000px;
    min-height: 100vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
}

section.about-me>p {
    margin: 0;
}

section.skills {
    min-height: 100vh;
    background-color: #2f4242;
    position: relative;
    cursor: grab;
}

section.skills:active {
    cursor: grabbing;
}

section.my-work {
    position: relative;
}

section.skills:before, section.my-work:before {
    font-size: 42px;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    background-color: #383c3c;
    padding: 10px;
}

section.skills:before {
    content: "My Tech Stack";
}

section.my-work:before {
    content: "My Work";
}

.sect-inner-container {
    max-width: 1920px;
    margin: auto;
    padding: 100px 40px;
}

section.my-work .sect-inner-container {
    max-width: 1200px;
}

.my-work-item {
    display: flex;
}

.my-work-item:not(:last-child) {
    margin-bottom: 100px;
}

.my-work-item-image-container {
    height: auto;
    width: 50%;
    margin-right: 40px;
}

.my-work-item-image-container>img {
    max-width: 100%;
}

.my-work-item-desc {
    flex: 1 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 32px;
}

.my-work-item-inner-desc {
    font-size: 20px;
    margin-top: 20px;
}

.my-work-item-inner-link {
    cursor: pointer;
    width: 200px;
    padding: 10px;
    text-align: center;
    border: 1px solid;
    color: #ffffff;
    text-decoration: none;
    font-size: 16px;
    margin-top: 20px;
}

.my-work-item-inner-link, .selection-item {
    transition: background-color 0.2s ease;
}

.my-work-item-inner-link:hover, .selection-item:hover {
    background-color: rgba(56, 60, 60, 0.6);
}

section.contact-me {
    padding: 40px;
    text-align: center;
    font-size: 24px;
}

.social-media {
    margin-top: 30px;
}

@media (max-width: 768px) {
    section.skills {
        pointer-events: none;
    }
    .selection {
        width: 100%;
    }
    #about-me-video {
        transform: translateX(-7%);
    }
    .my-work .sect-inner-container {
        padding: 100px 0;
    }
    .my-work-item {
        flex-flow: column;
    }
    .my-work-item-image-container {
        width: 100%;
    }
    .my-work-item-desc {
        margin-top: 10px;
        text-align: center;
        align-items: center;
    }
}